import React, { Component } from "react";
import { Link } from 'react-router-dom';
import Todos from "./Todofiles/Todos";
import AddTodo from "./Todofiles/AddTodo";
import axios from "axios";

export class Todo extends Component {
  state = {
    todos: [],
  };

  componentDidMount() {
    axios
      .get("https://jsonplaceholder.typicode.com/todos?_limit=5")
      .then((res) => this.setState({ todos: res.data }));
  }

  markComplete = (id) => {
    this.setState({
      todos: this.state.todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      }),
    });
  };

  delTodo = (id) => {
    axios
      .delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
      .then((res) =>
        this.setState({
          todos: [...this.state.todos.filter((todo) => todo.id !== id)],
        })
      );
  };

  addTodo = (title) => {
    /*const newTodo = {
        id: v4(),
        title,
        completed: false,
      };*/
    axios
      .post("https://jsonplaceholder.typicode.com/todos", {
        title,
        completed: false,
      })
      .then((res) => this.setState({ todos: [...this.state.todos, res.data] }));
  };

  render() {
    return (
      <div>
        <div className="col-10 offset-1 col-sm-10 offset-1 col-md-6 offset-md-3">
        <h1 style={projectHeader}>This Todolist uses api calls to simulate a real backend. <br/>As a result, it is quite slow.</h1>
        <div className="row">
          <Link style={ buttonStyle } to="/projects">Back</Link>
        </div>
          <AddTodo addTodo={this.addTodo} />
          <Todos
            todos={this.state.todos}
            markComplete={this.markComplete}
            delTodo={this.delTodo}
          />
        </div>
      </div>
    );
  }
}

const projectHeader = {
  padding: "1.2em 0em",
  fontSize: "calc( 20px + (25 - 20) * (100vw - 400px)/(800-400) )",
  color: "white",
  animation: "fadein1 4s"
};

const buttonStyle = {
  color: "white",
  float: "left",
  borderRadius: "4px",
  padding: "0.7em 1.2em",
  fontSize: "calc( 14px + (18 - 14) * (100vw - 400px)/(800-400) )",
  animation: "fadein2 4s"
}

export default Todo;
